import React from 'react';
import { BrowserRouter as Router, Route, NavLink } from 'react-router-dom';
import HomePage from './components/HomePage';
import RecipesPage from './components/RecipesPage';
import MealPrepPage from './components/MealPrepPage';

const App = () => {
  return (
    <Router>
      <div>
        <nav>
          <ul>
            <li>
              <NavLink exact to="/" activeClassName="active-link">Home</NavLink>
            </li>
            <li>
              <NavLink to="/recipes" activeClassName="active-link">Recipes</NavLink>
            </li>
            <li>
              <NavLink to="/mealprep" activeClassName="active-link">Meal Prep</NavLink>
            </li>
          </ul>
        </nav>

        <Route exact path="/" component={HomePage} />
        <Route path="/recipes" component={RecipesPage} />
        <Route path="/mealprep" component={MealPrepPage} />
      </div>
    </Router>
  );
};

export default App;
